.nav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    z-index: 1;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    transition-timing-function: ease-in;  
    transition: all 0.5s;  
}

.nav_black {
    background-color: #111;
}


.nav_logo {
    position: fixed;
    left: 0;
    width: 80px;
    object-fit: contain;
}

.nav_avatar {
   position: fixed;
    width: 30px;
    right: 20px;
    width: 30px;
}